import { createRouter, createWebHistory } from 'vue-router'
//import { createRouter, createWebHashHistory } from 'vue-router'
const ImageConverter = () => import('@/components/ImageConverter.vue')
const ImageMap = () => import('@/components/ImageMap.vue')
const BinaryViewer = () => import('@/components/BinaryViewer.vue')
const BatteryCalc = () => import('@/components/BatteryCalc.vue')
const ChangeLog = () => import('@/components/ChangeLog.vue')

const routes = [
  {
    path: '/',
    name: 'ImageConverter',
    component: ImageConverter
  },
  {
    path: '/image-map',
    name: 'ImageMap',
    component: ImageMap
  },
  {
    path: '/binary-viewer',
    name: 'BinaryViewer',
    component: BinaryViewer
  },
  {
    path: '/battery-calc',
    name: 'BatteryCalc',
    component: BatteryCalc
  },
  {
    path: '/change-logs',
    name: 'ChangeLog',
    component: ChangeLog
  }
  // ,
  // { 
  //   path: '/:catchAll(.*)', 
  //   component: NotFoundComponent,
  //   name: 'NotFound'
  // }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL), 
  //history: createWebHashHistory(), 
  //mode: 'history',
  routes
})

export default router 